export default {
	data() {
		return {
			jifen: '',
			daishen: '',
			username: '',
			phone: '',
			accounts: '',
			withdraw: '',
			money: ''
		};
	},
	onShow: function() {
		// 用户信息接口
		var _this = this
		// var url = _this.Global.url
		uni.request({
			url: 'https://exam.qhynice.top/index.php/Api/User/userinfo',
			data: {
				uid: uni.getStorageSync('id')
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			method: 'POST',
			success: res => {
				this.jifen = res.data.userinfo.jifen;
			}
		});
		// 待审核积分
		uni.request({
			url:'https://exam.qhynice.top/index.php/Api/User/pending',
			data: {
				uid: uni.getStorageSync('id')
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			method: 'POST',
			success: res => {
				this.daishen = res.data.data;
			}
		});
	},
	methods: {
		// 判断用户姓名
		name: function() {
			if (this.username == '' || this.username == null) {
				uni.showToast({
					title: '收款人姓名必填',
					duration: 2000,
					icon: 'none'
				});
				return false;
			} else if (!/^[\u4E00-\u9FA5]{2,4}$/.test(this.username)) {
				uni.showToast({
					title: '姓名有误',
					duration: 2000,
					icon: 'none'
				});
				return false;
			}
		},
		// 判断用户手机号
		iphone: function() {
			if (this.phone == '' || this.phone == null) {
				uni.showToast({
					title: '收款人电话不能为空',
					duration: 2000,
					icon: 'none'
				});
			} else if (!/^1[34578]\d{9}$/.test(this.phone)) {
				uni.showToast({
					title: '手机号码有误',
					duration: 2000,
					icon: 'none'
				});
				return false;
			}
		},
		// 判断输入银行账户/支付宝账号
		account: function() {
			if (this.accounts == '' || this.accounts == null) {
				uni.showToast({
					title: '请填写账户',
					duration: 2000,
					icon: 'none'
				});
			} else if (!/^(?:\w+\.?)*\w+@(?:\w+\.)+\w+|\d{9,11}$/.test(this.accounts)) {
				uni.showToast({
					title: '银行卡号/支付宝账号有误',
					duration: 2000,
					icon: 'none'
				});
				return false;
			}
		},
		change: function() {
			this.money = +this.withdraw - this.withdraw * 0.1;
		},
		sub: function() {
			if (this.username == '') {
				uni.showToast({
					title: '请填写账号信息',
					duration: 2000,
					icon: 'none'
				});
				return false;
			} else if (this.phone == '') {
				uni.showToast({
					title: '请填写手机号',
					duration: 2000,
					icon: 'none'
				});
				return false;
			} else if (this.accounts == '') {
				uni.showToast({
					title: '请填写账号信息',
					duration: 2000,
					icon: 'none'
				});
				return false;
			} else if (this.money == '') {
				uni.showToast({
					title: '请输入提现额度',
					duration: 2000,
					icon: 'none'
				});
				return false;
			} else {
				// var that = this
				// var url = this.Global.url
				uni.request({
					url: 'https://exam.qhynice.top/index.php/Api/User/withdraw',
					method: 'POST',
					data: {
						uid: uni.getStorageSync('id'),
						name: this.username,
						tel: this.phone,
						jifen: this.money,
						num: this.withdraw,
						account: this.accounts
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: res => {
						uni.redirectTo({
							url: 'integral'
						});
					}
				});
			}
		}
	}
};
